<template>
	<div class="row form-pagination mt-5 mb-5">
		<div class="col-12 col-sm-3 text-center text-sm-left">
			<button class="btn btn-light btn-left btn-lg" @click="prev_step()">
				Previous step
			</button>
		</div>
		<div class="col-12 col-sm-3 text-center">
			<button class="btn btn-white btn-lg" @click="status()">
				Status
			</button>
		</div>
		<div class="col-12 col-sm-3 text-center">
			<button class="btn btn-primary btn-lg" @click="fe_actions()">
				Run FE actions
			</button>
		</div>
		<div class="col-12 col-sm-3 text-center text-sm-right">
			<button class="btn btn-primary btn-right btn-lg" @click="next_step()">
				Next step
			</button>
		</div>
	</div>
	<p v-for="(value, key) in output">{{ value }}</p>
</template>

<script>
	import Flow from '@/logic/Flow.js';
	import State from '@/store/State.js';

	export default {
		props: {
		},
		components: {
		},
		data() {
			return {
				'output': [],
			}
		},
		methods: {
			message(string) {
				this.output.push(string);
			},
			next_step() {
				this.message('started nextstep');
				Flow.next_step().then(
					(response) => {
						this.message('finished nextstep, workflowStatus: ' + JSON.stringify( response.result.data.workflowStatus ));
						console.dir(response.result.data.workflowStatus);
					}
				);
			},
			status() {
				this.message('started status');
				Flow.status().then(
					(response) => {
						this.message('finished status, workflowStatus: ' + JSON.stringify( response.result.data.workflowStatus ));
						console.dir(response.result.data.workflowStatus);
					}
				);
			},
			fe_actions() {
				this.message('started get-html');
				Flow.get_html().then(
					(response) => {
						this.message('finished get-html');
						console.dir(response.result.data);
					}
				);
				this.message('started get-form');
				Flow.get_form().then(
					(response) => {
						this.message('finished get-form');
						console.dir(response.result.data);
					}
				);
			},
			prev_step() {
				this.message('previous step not yet implemented');
			},
		},
		computed: {
		},
		mounted() {
			this.message('started init');
			Flow.init_session().then(
				(response) => {
					this.message('finished init, created session: ' + response.result.data.session + ', workflow: ' + response.result.data.workflow);
					State.set_session_token(response.result.data.session);
				}
			);
		},
	}
</script>
