import State from '@/store/State.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Flow
*/
class Flow
{
	/**
	 * init_session
	*/
	init_session()
	{
		var flow_id = 200; // hardcoded value, will be solved in future

		let requestParams = {
			action_name: 'init',
			method: 'GET',
			url: State.endpoint_url() + 'online-sale-mw/osmapp/init/' + flow_id,
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response();
	}

	/**
	 * next_step
	*/
	next_step()
	{
		let requestParams = {
			action_name: 'nextstep',
			method: 'GET',
			url: State.endpoint_url() + 'online-sale-mw/osmapp/nextstep/' + State.get_session_token(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response();
	}

	/**
	 * status
	*/
	status()
	{
		let requestParams = {
			action_name: 'status',
			method: 'GET',
			url: State.endpoint_url() + 'online-sale-mw/osmapp/status/' + State.get_session_token(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response();
	}

	/**
	 * get_html
	*/
	get_html()
	{
		let requestParams = {
			action_name: 'get-html',
			method: 'GET',
			url: State.endpoint_url() + 'online-sale-mw/osmapp/get-html/' + State.get_session_token(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response();
	}

	/**
	 * get_forn
	*/
	get_form()
	{
		let requestParams = {
			action_name: 'get-form',
			method: 'GET',
			url: State.endpoint_url() + 'online-sale-mw/osmapp/get-form/' + State.get_session_token(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response();
	}
}

export default new Flow;
